		reload: function reload() {
			//ios
			var el2 = this.$refs.secondScroll;
			var lastY = 0;
			el2.addEventListener('touchstart', function (event) {
				lastY = event.touches[0].clientY;
			});
			//
			var scrollEle = this.$refs.secondScroll;
			//
			scrollEle.removeEventListener('touchmove', function (event) {
				event.preventDefault();
			}, false);
			//
			scrollEle.addEventListener('touchmove', function (event) {
				var top = event.touches[0].clientY;
				var scrollTop = scrollEle.scrollTop;
				var scrollHeight = scrollEle.scrollHeight;
				var clientHeight = scrollEle.clientHeight;
				var direction = lastY - top < 0 ? "up" : "down";
				if (scrollTop == 0 && direction == "up") {
					event.preventDefault();
				} else if (scrollTop >= scrollHeight - clientHeight && direction == "down") {
					event.preventDefault();
				}
				lastY = top;
			});
		},
