import BScroll from 'better-scroll'
import noData from '@/components/noData'
import back from '@/components/back'
import goodsNav from '@/page/prefRecommend/goodsNav'
import goodsArea from '@/page/prefRecommend/goodsArea'
import { setInterval } from 'timers'
import { introAdmin } from '../../config'


export default {
    components:{
        noData,
        back,
		goodsNav,
		goodsArea
    },
    data(){
        return{
			navArr:[],
			act:0,
			GoodsClasss:[],
			Goods:[],
			defaultImg: 'this.src="' + require('../../assets/prefRecommend/1.jpg') + '"',
			GoodsNum:0,
			ListDetails:this.$store.state.preList,
			specsShow:false,
			carShow:false,
			spcData:null,
			chooseDrShow:false,
			page: 1,
			haveMore: true,
			inputFlag: false,
			pageShow: false,
			showConfirm: false,
			act2:null,
			SpecialistHosGroupId:this.$store.state.preId,
			HosGroupName:'',
			list:[],
			drSearch:'',
			act3:null,
			user: this.$store.state.userInfo,
			title:"",
			actT:null,
			SpecialId:null,
			idxNum:0,
			GoodsTypeNo:'',
			istHos:false,
			yEvent:null,
			goodList:this.$store.state.goodList,
			vTop:0,
			allSpa:this.$store.state.allSpa,
			boxWidth:2500,
 			AreaId:this.$store.state.inspctArea?this.$store.state.inspctArea.AreaId:0,
			AreaName:this.$store.state.inspctArea?this.$store.state.inspctArea.AreaName:'',
			navNum:this.$store.state.navNum,
			sortFalg:false,
			tMenu:[], //
			sMenu:[], //
			numMenuList:this.$store.state.numMenu, //
			numMenu:0,
			subMenu:0, //
			GoodsCountList:[], //
			ClickCountList:{}, //
			SpecialistService:[],
			DoctorService:[],
			From:1,
			listShow1:true,
			listShow2:true,
			listShow3:true,
			listShow4:true,
			MallGoodsList:[],
			ChannelGoodsList:[],
			navMenu:null,
			scrollTopDistance:0,
			ajaxNum:0,
			storText:[
				{
					text:'',
					stor:0,
				},
				{
					text:'',
					stor:1,
				},
				{
					text:'',
					stor:2,
				},
				{
					text:'',
					stor:3,
				},
				{
					text:'',
					stor:4,
				},
			],
			sortNum:0,
			sortList:this.$store.state.sortList,
			sizeNum:20,
			tZIndex:false,
			btnColor:true,
			introAdmin:introAdmin,
			scrollY:'',
			isContLen:false,
			TypeId:this.$store.state.TypeId,
			clickNum:0,
            mtdMoney:"120",
            mtdNum:null,
			scBoolean:false,
			iSSeconed:false,
			saveVipData:this.$store.state.saveVipData,
			RecommondAmountList:[],
			GoodsCountSize:10,
			GoodsCountFrom:1,
        }
    },
    computed:{
        totalPrice(){
            let p = 0
            if(this.ListDetails && this.ListDetails.length){
                this.ListDetails.forEach(v => {
					if(v.GoodsSource == 1 || v.GoodsSource == 6 || v.GoodsSource == 7){
						 p += (v.Price * v.GoodsNum)
					}else{
						p += (v.VipPrice * v.GoodsNum)
					}
                })
            }
            return p.toFixed(2)
        },
		totalM(){
			let p = 0
			if(this.ListDetails && this.ListDetails.length){
			    this.ListDetails.forEach(v => {
			        p += Number(v.RecommondAmount) * v.GoodsNum
			    })
			}
			return p
		}
    },
    created(){

    },
    mounted(){

    },
    beforeDestroy(){
        window.onscroll = null
    },
	beforeRouteLeave(to, from, next) {
		window.onscroll = null
		this.$store.dispatch('pushnumMenu',null)
		this.listScroll.destroy()
		if(from.meta.keepAlive) {
			from.meta.scollTopPosition = this.scrollTopDistance;
		}
		next()
	},
	beforeRouteEnter(to, from, next) { //
		next(vm => {
			//vm.getMore()
			vm.reload()
			let flag = false
			vm.navNum = vm.$store.state.navNum
			vm.ListDetails = vm.$store.state.preList
			vm.numMenuList = vm.$store.state.numMenu
			vm.TypeId = vm.$store.state.TypeId
			vm.saveVipData = vm.$store.state.saveVipData
			vm.$nextTick(() => {
			   vm.scrollYCount()
			})
			if((from.path != vm.$store.state.prevUrl && vm.$store.state.prevUrl != null) || !vm.$store.state.prevUrl || vm.$store.state.isFresh || vm.AreaId != vm.$store.state.inspctArea.AreaId) {
				flag = true
			}
			if(flag) {
				let inspctA = vm.$store.state.inspctArea?vm.$store.state.inspctArea.AreaId:0
				if(vm.$store.state.goodList && vm.AreaId == inspctA){ //
					vm.goodList = vm.$store.state.goodList
					if(!vm.numMenuList){
						setTimeout(() => {
							vm.listScroll.scrollTo(0, - to.meta.scollTopPosition)
							//vm.$refs.secondScroll.scrollTop = to.meta.scollTopPosition
						}, 1)
					}
				}else{
					vm.ajaxNum = 0
					vm.numMenu = 0
					vm.From = 1
					vm.listShow1 = true
					vm.listShow2 = true
					vm.listShow3 = true
					vm.listShow4 = true
					vm.AreaId = vm.$store.state.inspctArea?vm.$store.state.inspctArea.AreaId:0
					vm.AreaName = vm.$store.state.inspctArea?vm.$store.state.inspctArea.AreaName:''
					vm.$store.state.goodList = []
					vm.$store.state.TypeId = []
					vm.TypeId = vm.$store.state.TypeId
					vm.getMenu()
				}
			} else {
				setTimeout(() => {
					vm.listScroll.scrollTo(0, - to.meta.scollTopPosition)
					//vm.$refs.secondScroll.scrollTop = to.meta.scollTopPosition
				}, 1)
			}
			if(vm.numMenuList){
				vm.numMenuState()
			}
			vm.listList()
// 			console.log(vm.tMenu)
		});
	},
    methods: {
		storTClick(p,idx){
			this.sortNum = idx
			this.sortFalg = false
			let tN = 0
			for(var i=0;i<this.tMenu.length;i++){
				let b = 0
				for(var j=0;j<this.tMenu[i].MallGoodsType.length;j++){
					if(this.tMenu[i].MallGoodsType[j].GoodsTypeId != this.navMenu.GoodsTypeId){
						tN += this.tMenu[i].MallGoodsType[j].GoodsCount
					}else{
						b = 1
						break
					}
				}
				if(b == 1){
					break
				}
			}
			let arr = []
			for(var i=tN;i< this.navMenu.GoodsCount+tN;i++){
				arr.push(this.GoodsCountList[i])
			}
			if(p.stor == 1){
				arr.sort(function(a, b) {
					return a.VipPrice - b.VipPrice;
				});
			}else if(p.stor == 2){
				arr.sort(function(a, b) {
					return b.VipPrice - a.VipPrice;
				});
			}else if(p.stor == 3){
				arr.sort(function(a, b) {
					return a.RecommondAmount - b.RecommondAmount;
				});
			}else if(p.stor == 4){
				arr.sort(function(a, b) {
					return b.RecommondAmount - a.RecommondAmount;
				});
			}else{
				arr.sort(function(a, b) {
					return b.GoodsOrder - a.GoodsOrder;
				});
			}
			//console.log(tN) this.navMenu.GoodsCount  this.GoodsCountList
			for(var i=tN;i<this.navMenu.GoodsCount+tN;i++){
				for(var j=0;j<arr.length;j++){
					this.GoodsCountList.splice(i,1,arr[j])
					arr.splice(j,1)
					break;
				}
			}
			if(this.sortList && this.sortList.length){
				this.sortList.forEach((v,k) => {
					this.navMenu.text = p.text
					this.navMenu.stor = p.stor
					if(this.navMenu.GoodsTypeId == v.GoodsTypeId){
						this.sortList.splice(k,1,this.navMenu)
					}else{
						this.sortList.push(this.navMenu)
					}
				})
			}else{
				this.navMenu.text = p.text
				this.navMenu.stor = p.stor
				this.sortList.push(this.navMenu)
			}
			this.$store.dispatch('pushsortList',this.sortList)

			this.$store.dispatch('pushgoodList',this.GoodsCountList)
			this.tabMenuClick(this.navMenu,this.subMenu)

		},
		sortFalgState(){
			if(this.sortFalg){
				this.sortFalg = false
			}else{
				this.sortFalg = true
			}
		},
		getMenu(){
			this.$get('PlatFormAPI/SpecialistHosGroup/GetAllRecommendMenu',this.$pick({
				DrId:this.user.Data.User.DoctorId,
				AreaId:this.AreaId,
				AreaName:this.AreaName,
			})).then(o => {
				this.tMenu = o.MallGoodsType
				this.sMenuMethods()
			})
		},
		sMenuMethods(){
			this.sMenu = this.tMenu[this.numMenu].MallGoodsType
			this.navMenu = this.tMenu[this.numMenu].MallGoodsType[0]
			this.GoodsCountList = []
			this.tMenu.forEach((v,k) => {
				for (var i = 0; i < v.GoodsCount; i++) {
					this.GoodsCountList.push(null)
				}
			})
			let contLen = this.tMenu[this.tMenu.length - 1].MallGoodsType[this.tMenu[this.tMenu.length - 1].MallGoodsType.length -1].GoodsCount
			if(contLen < 5){
				this.isContLen = true
			}
		//	this.tMenuType(this.tMenu[this.ajaxNum].MenuType,this.tMenu[this.ajaxNum].GoodsTypeId,this.tMenu[this.ajaxNum].GoodsTypeNo)
			setTimeout(()=>{
				this.numMenuState()
			},500)
		},
		numMenuState(){
			if(!this.numMenuList){
				this.MenuClick(this.tMenu[this.numMenu],0)
				return
			}
			if(this.numMenuList.MenuType == 4){//GoodsTypeNo MenuType
				for(var i=0;i<this.tMenu.length;i++){
					for(var j=0;j<this.tMenu[i].MallGoodsType.length;j++){
						if(this.tMenu[i].MallGoodsType[j].GoodsTypeNo == this.tMenu[i].GoodsTypeNo+this.numMenuList.GoodsTypeNo){
							this.numMenu = i
							this.sMenu = this.tMenu[i].MallGoodsType
							this.navMenu = this.tMenu[i].MallGoodsType[j]
							this.tabMenuClick(this.navMenu,j)
						}
					}
				}
			}else if(this.numMenuList.MenuType == 1){
				for(var i=0;i<this.tMenu.length;i++){
					if(this.tMenu[i].GoodsTypeNo == this.numMenuList.GoodsTypeNo){
						this.numMenu = i
						this.sMenu = this.tMenu[i].MallGoodsType
						this.navMenu = this.tMenu[i].MallGoodsType[0]
						this.MenuClick(this.tMenu[i],i)
					}
				}
			}
		},
		tMenuType(i,From,isReverse,isSizeNum){ //Menu,GoodsTypeId,GoodsTypeNo
			if(i.MenuType == 1){
				this.GetMallGoods(true,i,From,isSizeNum,isReverse)
			}else if(i.MenuType == 2){
				this.GetDoctorService(true,i,From,isSizeNum,isReverse)
			}else if(i.MenuType == 3){
				this.GetSpecialistService(true,i,From,isSizeNum,isReverse)
			}else if(i.MenuType == 4){
				this.GetChannelGoods(true,i,From,isSizeNum,isReverse)
			}
		},

		GetChannelGoods(h,mData,From,isSizeNum,isReverse){	// 38
			if(isReverse){
				if(From - this.sizeNum > 1){
					From = From - this.sizeNum + 1  
					isSizeNum = this.sizeNum
				}else{
					isSizeNum = From - 1
					From = 1
				}
			}
			if(this.listShow4){
				this.$get('PlatFormAPI/SpecialistHosGroup/GetAllRecommandChannelGoods',this.$pick({
					AreaId:this.AreaId,
					AreaName:this.AreaName,
					DrId:this.user.Data.User.DoctorId,
					From:From,
					Size:isSizeNum?isSizeNum:this.sizeNum,
					Version:1,
				})).then(o => {
					if (!o || o.length < this.sizeNum){
						this.listShow4 = false
					}
					else {
						this.listShow4 = true
					}
					if (h){
						o.forEach((v,k) => {
							this.ChannelGoodsList.push(v)
						})
					}
					else {
						this.ChannelGoodsList = o
					}
					for(var i=From-1;i<this.GoodsCountList.length;i++){
						for(var j=0;j<this.ChannelGoodsList.length;j++){
							if(!this.GoodsCountList[i]){
								let GoodsInfo = {
									SpecialistHosGroupId:this.ChannelGoodsList[j].SpecialistHosGroupId || 0,
									GoodsSource:this.ChannelGoodsList[j].GoodsSource,
									GoodsId:this.ChannelGoodsList[j].GoodsId,
									DrId:this.ChannelGoodsList[j].DrId,
								}
								this.getRecommed(GoodsInfo)
								this.GoodsCountList.splice(i,1,this.ChannelGoodsList[j])
								this.ChannelGoodsList.splice(j,1)
								break;
							}
						}
					}
					this.listList()
					if(this.listShow4){
						if(isReverse){
							if(From === 1){
								return
							}
							this.tMenuType(mData,From,true)
						}else{
							From = Number(From) + this.sizeNum
							this.tMenuType(mData,From)
						}
						//this.GetChannelGoods(true)
					}
					// else{
					// 	this.ajaxNum += 1
					// 	this.From = 1
					// 	this.tMenuType(this.tMenu[this.ajaxNum].MenuType,this.tMenu[this.ajaxNum].GoodsTypeId,this.tMenu[this.ajaxNum].GoodsTypeNo)
					// }

				})
			}
		},
		getRecommed(GoodsInfo){
			this.$post('PlatFormAPI/SpecialistHosGroup/GetRecommondAmount',{
				DrId:this.user.Data.User.DoctorId,
				Goods:[GoodsInfo]
			}).then(o => {
				this.RecommondAmountList.push(o[0])
				if(this.RecommondAmountList && this.RecommondAmountList.length){
					this.RecommondAmountList.forEach((v,k) => { //this.GoodsCountList.RecommondAmount = v.RecommondAmount
						this.GoodsCountList.forEach((i,y) => {
							if(i && i.GoodsSource && v && v.GoodsSource){
								if(v.GoodsSource == 3){
									if(v.GoodsSource == i.GoodsSource && v.Id == i.GoodsId){
										this.GoodsCountList[y].RecommondAmount = v.RecommondAmount
										this.RecommondAmountList.splice(k,1)
									}
								}if(v.GoodsSource == 4){
									if(v.GoodsSource == i.GoodsSource && v.SpecialistHosGroupId == i.SpecialistHosGroupId && v.Id == i.SpecialistGoodsId){
										this.GoodsCountList[y].RecommondAmount = v.RecommondAmount
										this.RecommondAmountList.splice(k,1)
									}	
								}else{
									if(v.GoodsSource == i.GoodsSource && v.SpecialistHosGroupId == i.SpecialistHosGroupId && v.Id == i.GoodsId){
										this.GoodsCountList[y].RecommondAmount = v.RecommondAmount
										this.RecommondAmountList.splice(k,1)
									}
								}
							}
						})
					})
				}
				
			})
			
		},
		GetSpecialistService(h,mData,From,isSizeNum,isReverse){	//
			if(isReverse){
				if(From - this.sizeNum > 1){
					From = From - this.sizeNum + 1  
					isSizeNum = this.sizeNum
				}else{
					isSizeNum = From - 1
					From = 1
				}
			}
			if(this.listShow3){
				this.$get('PlatFormAPI/SpecialistHosGroup/GetAllRecommandSpecialistService',this.$pick({
					DrId:this.user.Data.User.DoctorId,
					AreaId:this.AreaId,
					AreaName:this.AreaName,
					From:From,
					Size:isSizeNum?isSizeNum:this.sizeNum,
					Version:1,
				})).then(o => {
					if (!o || o.length < this.sizeNum){
						this.listShow3 = false
					}
					else {
						this.listShow3 = true
					}
					if (h){
						o.forEach(v => {
							this.SpecialistService.push(v)
						})
					}
					else {
						this.SpecialistService = o
					}


					let tNum = 0
					for(var i=0;i<this.tMenu.length;i++){ //
						if(this.tMenu[i].GoodsTypeId === mData.GoodsTypeId){
							break
						}
						tNum += this.tMenu[i].GoodsCount
					}
					for(var i=tNum+From-1;i<this.GoodsCountList.length;i++){
						for(var j=0;j<this.SpecialistService.length;j++){
							if(!this.GoodsCountList[i]){
								this.GoodsCountList.splice(i,1,this.SpecialistService[j])
								this.SpecialistService.splice(j,1)
								break;
							}
						}
					}
					this.listList()
					if(this.listShow3){
						if(isReverse){
							if(From === 1){
								return
							}
							this.tMenuType(mData,From,true)
						}else{
							From = Number(From) + this.sizeNum
							this.tMenuType(mData,From)
						}
					}
					// else{
					// 	this.ajaxNum += 1
					// 	this.From = 1
					// 	this.tMenuType(this.tMenu[this.ajaxNum].MenuType,this.tMenu[this.ajaxNum].GoodsTypeId,this.tMenu[this.ajaxNum].GoodsTypeNo)
					// }

				})
			}
		},
		GetDoctorService(h,mData,From,isSizeNum,isReverse){	//  40 
			if(isReverse){
				if(From - this.sizeNum > 1){
					From = From - this.sizeNum + 1  
					isSizeNum = this.sizeNum
				}else{
					isSizeNum = From - 1
					From = 1
				}
			}
			if(this.listShow2){
				this.$get('PlatFormAPI/SpecialistHosGroup/GetAllRecommandDoctorService',this.$pick({
					DrId:this.user.Data.User.DoctorId,
					From:From,
					Size:isSizeNum?isSizeNum:this.sizeNum,
					GoodsTypeNo:mData.GoodsTypeNo,//
					Version:1,
				})).then(o => {
					if (!o || o.length < this.sizeNum){
						this.listShow2 = false
					}
					else {
						this.listShow2 = true
					}
					if (h){
						o.forEach(v => {
							this.DoctorService.push(v)
						})
					}
					else {
						this.DoctorService = o
					}

					let tNum = 0
					for(var i=0;i<this.tMenu.length;i++){ //
						if(this.tMenu[i].GoodsTypeId === mData.GoodsTypeId){
							break
						}
						tNum += this.tMenu[i].GoodsCount
					}
					for(var i=tNum+From-1;i<this.GoodsCountList.length;i++){
						for(var j=0;j<this.DoctorService.length;j++){
							if(!this.GoodsCountList[i]){
								if(this.DoctorService[j].GoodsSource == 4){
									let GoodsInfo = {
										SpecialistHosGroupId:this.DoctorService[j].SpecialistHosGroupId || 0,
										GoodsSource:this.DoctorService[j].GoodsSource,
										GoodsId:this.DoctorService[j].SpecialistGoodsId,
										DrId:this.DoctorService[j].DrId,
									}
									this.getRecommed(GoodsInfo)
								}
								this.GoodsCountList.splice(i,1,this.DoctorService[j])
								this.DoctorService.splice(j,1)
								break;
							}
						}
					}
					
					this.listList()
					if(this.listShow2){
						if(isReverse){
							if(From === 1){
								return
							}
							this.tMenuType(mData,From,true)
						}else{
