		GetMallGoods(h,mData,From,isSizeNum,isReverse){	//
			if(!mData.GoodsTypeId){
				return
			}
			let type = mData.GoodsTypeId
			if(isReverse){
				if(From - this.sizeNum > 1){
					From = From - this.sizeNum + 1  
					isSizeNum = this.sizeNum
				}else{
					isSizeNum = From - 1
					From = 1
				}
			}
			if(this.listShow1){
				this.$get('PlatFormAPI/SpecialistHosGroup/GetAllRecommandMallGoods',this.$pick({
					AreaId:this.AreaId,
					AreaName:this.AreaName,
					DrId:this.user.Data.User.DoctorId,
					GoodsTypeId:type,
					From:From,
					Size:isSizeNum?isSizeNum:this.sizeNum,
					Version:1,
				})).then(o => {
					if (!o || o.length < this.sizeNum){
						this.listShow1 = false
					}
					else {
						this.listShow1 = true
					}
					if (h){
						o.forEach(v => {
							this.MallGoodsList.push(v)
						})
					}
					else {
						this.MallGoodsList = o
					}
					
					if(this.navMenu.ParentId == type){
						this.navMenu.text = ''
						this.navMenu.stor = 0
					}

					let tNum = 0
					for(var i=0;i<this.tMenu.length;i++){ //
						if(this.tMenu[i].GoodsTypeId === mData.GoodsTypeId){
							break
						}
						tNum += this.tMenu[i].GoodsCount
					}
					for(var i=tNum+From-1;i<this.GoodsCountList.length;i++){
						for(var j=0;j<this.MallGoodsList.length;j++){
							let GoodsInfo = {
								SpecialistHosGroupId:0,
								GoodsSource:this.MallGoodsList[j].GoodsSource,
								GoodsId:this.MallGoodsList[j].GoodsId,
								DrId:this.MallGoodsList[j].DrId,
							}
							this.getRecommed(GoodsInfo)
							this.GoodsCountList.splice(i,1,this.MallGoodsList[j])
							this.MallGoodsList.splice(j,1)
							break
						}
					}
					//console.log(this.GoodsCountList)
					this.listList()
					if(this.listShow1){
						if(isReverse){
							if(From === 1){
								return
							}
							this.tMenuType(mData,From,true)
						}else{
							From = Number(From) + this.sizeNum
							this.tMenuType(mData,From)
						}
						
					}

				})
			}
		},

