				this.scrollY = Math.abs(Math.round(pos.y));
				var secondNav = this.$refs.secondNav
				const sh = secondNav.length?Number(secondNav[0].clientHeight):Number(secondNav.clientHeight)
				const len =  this.GoodsCountList.length
				if(this.scrollY > Number(sh * len)){
					return
				}
				//console.log(this.scrollY)
                this.scrollEnd(this.scrollY)
			  })
		},
		scrollEnd(top){
			this.scrollTopDistance = top
			var secondNav = this.$refs.secondNav
			const sh = secondNav.length?Number(secondNav[0].clientHeight):Number(secondNav.clientHeight)  //
			const n = Number(top / sh) //
			//this.tMenu 
			//this.sMenu 
			let tLNum = 0
			for(var i=0;i<this.tMenu.length;i++){ //
				tLNum += this.tMenu[i].GoodsCount
				if(tLNum > n){
					this.numMenu = i
					this.sMenu = this.tMenu[i].MallGoodsType
					break
				}
			}
			//console.log(this.TypeId)
			if(this.TypeId && this.TypeId.length){
				let nTy = 0
				for(var j=0;j<this.TypeId.length;j++){
					if(this.TypeId[j] === this.tMenu[this.numMenu].GoodsTypeId){
						nTy = 1
					}
				}
				//console.log(nTy)
				if(nTy === 0){
					this.TypeId.push(this.tMenu[this.numMenu].GoodsTypeId)
					this.listShow1 = true
					this.listShow2 = true
					this.listShow3 = true
					this.listShow4 = true
					if(this.tMenu[this.numMenu].GoodsCount - this.sizeNum <= 0){
						this.tMenuType(this.tMenu[this.numMenu],1)
					}else{
						this.tMenuType(this.tMenu[this.numMenu],this.tMenu[this.numMenu].GoodsCount,true)
					}
					// this.GoodsCountFrom = 1
					// this.GoodsCountSize = 10
					// this.clickMallGoods(this.tMenu[this.numMenu].GoodsTypeId,this.tMenu[this.numMenu].GoodsCount)
				}
				if(this.numMenu < this.tMenu.length){
					if(tLNum - Math.ceil(n) < 4){
						let nTz = 0
						for(var j=0;j<this.TypeId.length;j++){
							if(this.TypeId[j] === this.tMenu[this.numMenu+1].GoodsTypeId){
								nTz = 1
							}
						}
						if(nTz === 0){
							this.TypeId.push(this.tMenu[this.numMenu+1].GoodsTypeId)
							this.listShow1 = true
							this.listShow2 = true
							this.listShow3 = true
							this.listShow4 = true
							this.tMenuType(this.tMenu[this.numMenu+1],1)
							if(this.tMenu[this.numMenu+1].GoodsCount < 3){
								this.TypeId.push(this.tMenu[this.numMenu+2].GoodsTypeId)
								this.tMenuType(this.tMenu[this.numMenu+2],1)
							}
						}
					}
					// if(Math.ceil(n) - (tLNum - this.tMenu[this.numMenu].GoodsCount)  < 2){
					// 	let nTz = 0
					// 	for(var j=0;j<this.TypeId.length;j++){
					// 		if(this.numMenu > 1){
					// 			if(this.TypeId[j] === this.tMenu[this.numMenu-1].GoodsTypeId){
					// 				nTz = 1
					// 			}
					// 		}
					// 	}
					// 	if(nTz === 0){
					// 		this.TypeId.push(this.tMenu[this.numMenu-1].GoodsTypeId)
					// 		this.listShow1 = true
					// 		this.listShow2 = true
					// 		this.listShow3 = true
					// 		this.listShow4 = true
					// 		this.tMenuType(this.tMenu[this.numMenu-1],1)
					// 		if(this.tMenu[this.numMenu-1].GoodsCount < 3){
					// 			this.TypeId.push(this.tMenu[this.numMenu-2].GoodsTypeId)
					// 			this.tMenuType(this.tMenu[this.numMenu-2],1)
					// 		}
					// 	}
					// }


				}
				
			}else{
				this.TypeId.push(this.tMenu[this.numMenu].GoodsTypeId)
				this.listShow1 = true
				this.listShow2 = true
				this.listShow3 = true
				this.listShow4 = true
				this.tMenuType(this.tMenu[this.numMenu],1)
				// this.GoodsCountFrom = 1
				// this.GoodsCountSize = 10
				// this.clickMallGoods(this.tMenu[this.numMenu].GoodsTypeId,this.tMenu[this.numMenu].GoodsCount)
			}
			
			let tN = 0
			for(var i=0;i<this.tMenu.length;i++){//
				let b = 0
				for(var j=0;j<this.tMenu[i].MallGoodsType.length;j++){
					tN += this.tMenu[i].MallGoodsType[j].GoodsCount
					if(tN > n){
						this.navMenu = this.tMenu[i].MallGoodsType[j]
						b = 1
						break
					}
				}
				if(b == 1){
					break
				}
			}
			this.sMenu.forEach((v,k)=> {
				if(v.GoodsTypeId == this.navMenu.GoodsTypeId){
					this.subMenu = k
				}
			})
			const tabMenu = this.$refs.tabMenu
			const lw = tabMenu.offsetWidth //323
			const tabMenuList = this.$refs.tabMenuList
			let lt = 0
			if(this.subMenu < 1){
				tabMenu.scrollLeft = 0
			}else{
				for(var i=0;i<this.subMenu;i++){
					lt += tabMenuList[i].offsetWidth + 30
					if(lt >= lw){
						tabMenu.scrollLeft = lt - lw + tabMenuList[i].offsetWidth+30
					}else{
						tabMenu.scrollLeft = 0
					}
				}
			}
			this.strotNavMenu()
		},

		strotNavMenu(){
			if(this.sortList && this.sortList.length){
				this.sortList.forEach((v,k) => {
				    if(v.GoodsTypeId == this.navMenu.GoodsTypeId){
						this.navMenu = v
					}
				})
			}
		},
		MenuClick(i,idx){
            this.listScroll.stop()
			this.numMenu = idx
			this.iSSeconed = false
			this.sortFalg = false
			//this.clickNum = 1
			// if(i.MenuType == 1){
			// 	if(this.TypeId.indexOf(i.GoodsTypeId) === -1){
			// 		this.GoodsCountFrom = 1
			// 		this.GoodsCountSize = 10
			// 		this.clickMallGoods(i.GoodsTypeId,i.GoodsCount)
			// 	}
			// }
			if(this.TypeId.indexOf(i.GoodsTypeId) === -1){
				this.listShow1 = true
				this.listShow2 = true
				this.listShow3 = true
				this.listShow4 = true
				this.tMenuType(i,1)

				if(this.TypeId && this.TypeId.length){
					let n = 0
					for(var j=0;j<this.TypeId.length;j++){
						if(this.TypeId[j] === i.GoodsTypeId){
							n = 1
						}
					}
					if(n === 0){
						this.TypeId.push(i.GoodsTypeId)
					}
				}else{
					this.TypeId.push(i.GoodsTypeId)
				}
				this.$store.dispatch('pushTypeId',this.TypeId)
			}
			console.log(this.TypeId)
			//this.$store.dispatch('pushnumMenu', idx)
			var secondNav = this.$refs.secondNav
			const sh = secondNav.length?Number(secondNav[0].clientHeight):Number(secondNav.clientHeight)
			let tN = 0
			if(idx == 0){
				this.listScroll.scrollTo(0,0)
				return
			}
			for(var i=0;i<idx;i++){
				tN += this.tMenu[i].GoodsCount
			}
			let tHigth = tN * sh
			this.listScroll.scrollTo(0, -tHigth)
			this.scrollTopDistance = (tHigth)
		},


		tabMenuClick(o,idx){
            this.listScroll.stop()
			this.subMenu = idx
			this.iSSeconed = false
			this.sortFalg = false
			var secondNav = this.$refs.secondNav
			const sh = secondNav.length?Number(secondNav[0].clientHeight):Number(secondNav.clientHeight)
			let tN = 0
			for(var i=0;i<this.tMenu.length;i++){//
				let b = 0
				for(var j=0;j<this.tMenu[i].MallGoodsType.length;j++){
					if(o.GoodsTypeId != this.tMenu[i].MallGoodsType[j].GoodsTypeId){
						tN += this.tMenu[i].MallGoodsType[j].GoodsCount
					}else{
						b = 1
						break
					}
				}
				if(b == 1){
					break
				}
			}
			this.listScroll.scrollTo(0, -tN * sh)
			this.scrollTopDistance = (tN * sh)
		},
		iSSeconedShow(){
			this.iSSeconed = true
		},
		iSSeconedhide(){
			this.iSSeconed = false
		},
		sBgHide(){
			this.specsShow = false
			this.carShow = false
			this.chooseDrShow = false
		},
		reload(){
			//ios
			let el2 = this.$refs.secondScroll
			let lastY = 0;
			el2.addEventListener('touchstart', function(event) {
				lastY = event.touches[0].clientY;
			});
			//
			let scrollEle =this.$refs.secondScroll
			//
			scrollEle.removeEventListener('touchmove', function (event) {
				event.preventDefault();
			}, false);
			//
			scrollEle.addEventListener('touchmove', function(event) {
				let top = event.touches[0].clientY;
				let scrollTop = scrollEle.scrollTop;
				let scrollHeight = scrollEle.scrollHeight;
				let clientHeight = scrollEle.clientHeight;
				let direction = (lastY - top) < 0 ? "up" : "down";
				if (scrollTop == 0 && direction == "up") {
				  event.preventDefault();
				} else if (scrollTop >= (scrollHeight - clientHeight) && direction == "down") {
				  event.preventDefault();
				}
				lastY = top;
			});

		},
        goIntro(){
			//APP
			let _url = this.introAdmin + "index.html#/prefIntro?fromAPP=1"
			let obj = {
				title : "",
				linkUrl : _url
			}
			this.$bridge.callhandler('ServiceIntroduction', JSON.stringify(obj))
		}
    }
}



